body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --b1: Roboto;
  --h1: Inter;

  /* font sizes */
  --b1-size: 26px;
  --font-size-2xl: 21px;
  --h1-size: 69px;
  --font-size-22xl: 41px;
  --font-size-36xl: 55px;
  --font-size-11xl: 30px;

  /* Colors */
  --color-gray: #000417;
  --color-darkorange: #df8601;
  --color-white: #fff;
  --color-yellow: #eaef03;
  --color-mediumspringgreen: #01df8f;
  --color-deeppink: #ff00c6;
  --color-aqua: #00f0ff;

  /* Gaps */
  --gap-xl: 20px;
  --gap-sm: 14px;
  --gap-21xl: 40px;
  --gap-2xl: 21px;

  /* Paddings */
  --padding-6xl: 25px;
  --padding-5xs: 8px;
  --padding-xl: 20px;
  --padding-12xs: 1px;
  --padding-10xl: 29px;
  --padding-4xs: 9px;
  --padding-9xl: 28px;
  --padding-xs: 12px;
  --padding-3xs: 10px;
  --padding-9xs: 4px;
}
